<template>
  <div class="about">
    <v-container>
      <v-btn text @click="goBack">
        <span class="material-icons">keyboard_arrow_left</span>Back to Results
      </v-btn>
      <h2>{{ selectedProvider.Prov_Name }}, {{ selectedProvider.Degree }}</h2>
      <hr />
      <v-layout row wrap>
        <v-col cols="12" sm="6" md="4" order-sm="2" order-md="2">
          <v-img
            error="imgLoadError"
            width="200"
            contain
            v-bind:src="`${imagePath}/${selectedProvider.NPI}.jpg`"
          ></v-img>
        </v-col>
        <v-col xs="12" sm="6" md="8" order-sm="1" order-md="1">
          <v-row>
            <v-col col-12>
              <h2>
                <i class="fas fa-stethoscope chsli-icon"></i>Specialty
              </h2>
              <div>{{ selectedProvider.Speciality }}</div>
            </v-col>
          </v-row>
          <v-row>
            <v-col col-12>
              <h2>
                <i class="fas fa-hospital chsli-icon"></i>Hospital Affiliations
              </h2>
              <div
                v-for="(aff, index) in selectedProvider.Hospital_Affiliations"
                v-bind:key="index"
              >
                <div>{{ aff.Hospital_Name }}</div>
              </div>
            </v-col>
          </v-row>
        </v-col>
      </v-layout>
      <v-layout row wrap>
        <v-col col-12>
          <h2>
            <i class="fas fa-graduation-cap chsli-icon"></i>Education and Training
          </h2>
          <div v-for="(ed, index) in selectedProvider.Institutions" v-bind:key="index">
            <div>{{ ed.Type }}, {{ ed.Institution_Name }}, {{ ed.City }}, {{ ed.State }}</div>
          </div>
        </v-col>
      </v-layout>
      <v-layout row wrap>
        <v-col cols="12">
          <h2>
            <i class="fas fa-map-marker-alt chsli-icon"></i>Addresses
          </h2>
          <v-layout row wrap>
            <v-col
              md="4"
              sm="6"
              cols="12"
              v-for="(loc, index) in selectedProvider.Location"
              v-bind:key="index"
            >
              <v-card color="#f1ebe5">
                <v-card-text>
                  <div>{{ loc.Practice }}</div>
                  <div>{{ loc.Address }}</div>
                  <div>{{ loc.City }}, {{ loc.State }} {{ loc.Zip }}</div>
                  <div>
                    <i class="fas fa-phone chsli-icon"></i>
                    {{ loc.Phone }}
                  </div>
                  <div>
                    <i class="fas fa-fax chsli-icon"></i>
                    {{ loc.Fax }}
                  </div>
                </v-card-text>
              </v-card>
            </v-col>
          </v-layout>
        </v-col>
      </v-layout>
      <v-layout row wrap>
        <v-col xs="12">
          <GmapMap
            ref="gmap"
            :center="defaultCenter"
            :zoom="15"
            map-type-id="roadmap"
            style="width: 100%; height: 400px"
          >
            <gmap-info-window
              :options="infoOptions"
              :position="infoWindowPos"
              :opened="infoWinOpen"
              @closeclick="infoWinOpen = false"
            ></gmap-info-window>
            <GmapMarker
              :key="index"
              v-for="(m, index) in markers"
              :position="m.position"
              title="label"
              :clickable="true"
              :draggable="true"
              @click="toggleInfoWindow(m, index)"
            />
          </GmapMap>
        </v-col>
      </v-layout>
      <v-btn text @click="goBack">
        <span class="material-icons">keyboard_arrow_left</span>Back to Results
      </v-btn>
    </v-container>
  </div>
</template>

<script>
import Config from "../config";

export default {
  name: "Provider",
  data: function() {
    return {
      infoWinOpen: false,
      infoWindowPos: {},
      infoOptions: {
        content: "",
        //optional: offset infowindow so it visually sits nicely on top of our marker
        pixelOffset: {
          width: 0,
          height: -35
        }
      },
      // npi: "1790762623",
      // npi: "1255431276",
      npi: this.$route.params.npi,
      imagePath: Config.imageUrl,
      markers_test: [
        {
          position: {
            lat: 40.9550594,
            lng: -73.0053853
          },
          infoText: "<strong>SFH SOUTH BAY CARDIO</strong>"
        },
        {
          position: {
            lat: 40.7550594,
            lng: -73.0053853
          },
          infoText: "<strong>SFH SOUTH BAY CARDIO 2</strong>"
        }
      ]
    };
  },
  computed: {
    defaultCenter: function() {
      // see if one of the addresses has a valid position
      // if no, default
      for (let i = 0; i < this.markers.length; i++) {
        if (
          this.markers[i].position.lat != "" &&
          this.markers[i].position.lng != ""
        ) {
          return {
            lat: parseFloat(this.markers[i].position.lat),
            lng: parseFloat(this.markers[i].position.lng)
          };
        }
      }

      return { lat: 40.7891, lng: -73.135 };
    },
    markers: function() {
      if (this.selectedProvider.Location) {
        return this.selectedProvider.Location.filter(
          loc => loc.Latitude != "" && loc.Longitude != ""
        ).map(loc => {
          return {
            position: {
              lat: parseFloat(loc.Latitude),
              lng: parseFloat(loc.Longitude)
            },
            infoText: `<strong>${loc.Practice}</strong>`
          };
        });
      } else {
        return [];
      }
    },
    selectedProvider: function() {
      return this.$store.getters.PROVIDER_LIST.find(el => el.NPI === this.npi);
    }
  },
  watch: {
    "$route.params": {
      // check for new params
      handler(newValue) {
        this.npi = newValue.npi;
      },
      immediate: true
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.$refs.gmap.$mapPromise.then(() => {
        // this.$refs.gmap.$mapObject.fitBounds(this.googleMapBounds);
        this.generateBounds("mounted");
      });
    });
  },
  methods: {
    toggleInfoWindow: function(marker, idx) {
      // when clicking on map pin, display popup
      this.infoWindowPos = marker.position;
      this.infoOptions.content = marker.infoText;

      //check if its the same marker that was selected if yes toggle
      if (this.currentMidx == idx) {
        this.infoWinOpen = !this.infoWinOpen;
      }
      //if different marker set infowindow to open and reset current marker index
      else {
        this.infoWinOpen = true;
        this.currentMidx = idx;
      }
    },
    imgLoadError() {
      console.log("no image");
    },
    generateBounds(fromWhere) {
      if (this.markers.length > 1) {
        const bounds = new google.maps.LatLngBounds();
        for (let m of this.markers) {
          bounds.extend(m.position);
        }
        this.$refs.gmap.$mapObject.fitBounds(bounds);
      }
    },
    goBack: function() {
      this.$router.back();
    }
  }
};
</script>

<style>
.chsli-icon {
  margin-right: 8px;
  font-size: 20px;
}
</style>
